.box{
  margin-top: 20px;
display: flex;
justify-content: center;
}
.profile {
    padding-top: 40px;
    padding-bottom: 40px;
    background-color: white;
    width: 500px;
    display: flex;
    border-radius: 4px;
    flex-direction: column;
    align-items: center;
    box-shadow: rgb(0 0 0 / 20%) 0px 3px 3px -2px,
    rgb(0 0 0 / 14%) 0px 3px 4px 0px, rgb(0 0 0 / 12%) 0px 1px 8px 0px;
}
.avatar {
  border-radius: 50%;
  box-shadow: rgb(0 0 0 / 20%) 0px 3px 3px -2px,
    rgb(0 0 0 / 14%) 0px 3px 4px 0px, rgb(0 0 0 / 12%) 0px 1px 8px 0px;
  width: 300px;
  height: 300px;
}
.description {
}

.textDescription{
  padding-top: 30px;
  padding-bottom: 20px;
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 10px;
}
.name {

font-family: 'Ubuntu', sans-serif;
}
.tag {
  font-family: 'Fira Sans', sans-serif;
  color: rgb(189, 188, 188);

}
.location {
  font-family: 'Fira Sans', sans-serif;
  color: rgb(189, 188, 188);
}

.stats {
    list-style: none;
    display: flex;

    gap: 20px
}
.statsList{
    width: 120px;
    height: 80px;
    display: flex;
    flex-direction: column;
    border-radius: 4px;
    gap: 5px;
    align-items: center;
    justify-content: center;
    background-color:rgb(212, 218, 211);
    box-shadow: rgb(0 0 0 / 20%) 0px 3px 3px -2px,
    rgb(0 0 0 / 14%) 0px 3px 4px 0px, rgb(0 0 0 / 12%) 0px 1px 8px 0px;

}
.label {
  color: rgba(27, 8, 8, 0.5);
font-family: 'Ubuntu', sans-serif;

}
.quantity {
  font-family: 'Fira Sans', sans-serif;
  font-weight: 600;
}
